import { RouterModule, Routes } from '@angular/router';
import { VehicleRequestComponent } from './vehicle-request.component';
import { ModuleWithProviders } from '@angular/core';

const vehicleRequestRoutes: Routes = [
    {
        path: '', component: VehicleRequestComponent
    }
];

export const vehicleRequestRouting: ModuleWithProviders = RouterModule.forChild(vehicleRequestRoutes);



