import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VehicleRequestComponent } from './vehicle-request.component';
import { vehicleRequestRouting } from './vehicle-request.routing';
import { SharedModule } from '../shared/shared.module';
import { TravelService } from '../services/travel.service';
import { FormsModule } from '@angular/forms';

@NgModule({
  imports: [
    CommonModule,
    vehicleRequestRouting,
    SharedModule,
    FormsModule
  ],
  declarations: [VehicleRequestComponent],
  providers: [
    TravelService
  ]
})
export class VehicleRequestModule { }



